import React, { useCallback, useMemo } from 'react'
import styled from 'styled-components'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import Page from 'components/layout/Page'
import BigNumber from 'bignumber.js'
import { Heading } from 'uikit'
import Spacer from 'components/Spacer'
import { useBondPriceInEstimatedTWAP, useCashPriceInEstimatedTWAP } from 'hooks/useExitBoardroom'
import { getDisplayBalance } from 'utils/formatBalance'
import { useTreasury } from 'hooks/useContract'
import UnlockButton from 'components/UnlockButton'
import LaunchCountdown from 'components/LaunchCountdown'
import useTokenBalance from 'hooks/useTokenBalance'
import { QuoteToken } from 'config/constants/types'
import ExchangeCard from './components/ExchangeCard'
import ExchangeStat from './components/ExchangeStat'

const launchTime = new Date(1616730082000)

const BOND_REDEEM_PRICE = 1.01
const BOND_REDEEM_PRICE_BN = new BigNumber(10).pow(18).multipliedBy(101).dividedBy(100)

const StyledHeader = styled.div`
  justify-content: center;
  display: flex;
  flex-direction: row;
  margin-bottom: ${(props) => props.theme.spacing[5]}px;
  width: 960px;

  > * {
    flex: 1;
    height: 84px;
    margin: 0 ${(props) => props.theme.spacing[2]}px;
  }

  @media (max-width: 768px) {
    flex-direction: column;
    width: 100%;
  }
`

const BondHeader = () => (
  <Heading as="h1" size="lg" mb="50px" style={{ textAlign: 'center' }}>
    ðŸ’° Bonds
  </Heading>
)

const Bond: React.FC = () => {
  const { account } = useWallet()
  const contract = useTreasury()
  const bondBalance = useTokenBalance(QuoteToken.SBO)
  const cashStat = useCashPriceInEstimatedTWAP()
  const bondStat = useBondPriceInEstimatedTWAP()

  const handleBuyBonds = useCallback(
    async (amount: string) => {
      await contract.methods.buyBonds(amount, cashStat).send({ from: account })
    },
    [cashStat, account, contract.methods],
  )

  const handleRedeemBonds = useCallback(
    async (amount: string) => {
      await contract.methods.redeemBonds(amount, bondStat).send({ from: account })
    },
    [bondStat, account, contract.methods],
  )
  const isBondRedeemable = useMemo(() => cashStat.gt(BOND_REDEEM_PRICE_BN), [cashStat])
  const isBondPurchasable = useMemo(() => Number(bondStat) < 1.0, [bondStat])

  const isLaunched = Date.now() >= launchTime.getTime()

  if (!isLaunched) {
    return (
      <Page>
        <BondHeader />
        <LaunchCountdown deadline={launchTime} />
      </Page>
    )
  }
  return (
    <Page>
      <BondHeader />
      {account ? (
        <>
          <StyledHeader>
            <ExchangeStat
              tokenName={QuoteToken.SDO}
              description="Last-Hour TWAP Price"
              price={cashStat?.toFixed(3) || '-'}
            />
            <ExchangeStat
              tokenName={QuoteToken.SBO}
              description="Current Price"
              price={bondStat?.toFixed(3) || '-'}
            />
          </StyledHeader>
          <StyledBond>
            <StyledCardWrapper>
              <ExchangeCard
                action="Purchase"
                fromToken={QuoteToken.SDO}
                toToken={QuoteToken.SBO}
                priceDesc={
                  !isBondPurchasable
                    ? 'SDO is over $1'
                    : `${Math.floor(100 / Number(bondStat) - 100)}% return when SDO > $1`
                }
                onExchange={handleBuyBonds}
                disabled={!bondStat || isBondRedeemable}
              />
            </StyledCardWrapper>
            <Spacer />
            <StyledCardWrapper>
              <ExchangeCard
                action="Redeem"
                fromToken={QuoteToken.SBO}
                toToken={QuoteToken.SDO}
                priceDesc={`${getDisplayBalance(bondBalance)} SDO Available`}
                onExchange={handleRedeemBonds}
                disabled={!bondStat || bondBalance.eq(0) || !isBondRedeemable}
                disabledDescription={!isBondRedeemable ? `Enabled when SDO > $${BOND_REDEEM_PRICE}` : null}
              />
            </StyledCardWrapper>
          </StyledBond>
        </>
      ) : (
        <UnlockButton mt="8px" fullWidth />
      )}
    </Page>
  )
}

const StyledBond = styled.div`
  display: flex;
  width: 960px;
  @media (max-width: 768px) {
    width: 100%;
    flex-flow: column nowrap;
    align-items: center;
  }
`

const StyledCardWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  @media (max-width: 768px) {
    width: 80%;
  }
`

export default Bond
